.info {
  font-family: "Nunito Sans", sans-serif;
  padding: 2em;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  min-height: calc(100vh - 100px);
  @media (max-width: 300px) {
    padding: 2em 1em;
  }
  .back {
    padding: 0.5em 1.5em;
    background: #fff;
    -webkit-box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
    font-family: "Nunito Sans", sans-serif;
    cursor: pointer;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    font-weight: 600;
    i {
      margin: 0 0.25em 0 0;
    }
    &:hover {
      background: rgba(100, 100, 100, 0.1);
      -webkit-box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
      box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
    }
  }
  .inner-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    span {
      font-size: 0.9;
      font-weight: 700;
    }
    .infoimg {
      width: 400px;
      height: 400px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      img {
        width: 100%;
        height: auto;
      }
    }
    .col-2 {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
      justify-content: space-around;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      & > div {
        margin: 0 2em;
        @media (max-width: 900px) {
          text-align: center;
          margin: 0;
        }
      }
      h1 {
        margin: 1em 0;
      }
      p {
        margin: 0.5em 0;
      }
    }
    .border {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      @media (max-width: 900px) {
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
      }
      ul {
        margin: 1em;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        li {
          border: 1px solid #555;
          padding: 0.25em;
          margin: 0.25em 0.25em;
        }
      }
    }
  }
  .error404 {
    padding: 2em;
    text-align: center;
    .in-er {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      h1 {
        margin: 0 0.25em;
        font-size: 5rem;
        font-weight: 500;
      }
    }
  }
}
.darkinfo {
  background: hsl(207, 26%, 17%);
  color: hsl(0, 0%, 100%);
  .back {
    background: hsl(209, 23%, 22%);
    color: hsl(0, 0%, 100%);
  }
}
