@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@100;200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;font-family:"Nunito Sans",sans-serif;box-shadow:0 0 5px 5px rgba(0,0,0,.1);min-height:100px;padding:0 4em;color:#000;transition:.4s}@media(max-width: 600px){.navbar{padding:0 1em}}@media(max-width: 600px){.navbar h1{font-size:5vw}}.navbar h3{cursor:pointer}@media(max-width: 600px){.navbar h3{font-size:3.5vw}}.navbar h3 i{margin:0 .5em}.darknav{color:#fff;background:#2b3945}.lightnav{color:#000;background:#fff}
.card{width:250px;min-height:320px;font-family:"Nunito Sans",sans-serif;box-shadow:2px 0 10px 1px rgba(0,0,0,.1);border-radius:5px;margin:2em;overflow:hidden;background:#fff;color:#000}.card .head{width:100%;height:50%;overflow:hidden}.card .head img{object-fit:cover;width:100%;height:150px}.card .body{padding:.75em 1.5em}.card .body h3{margin:0 0 1em 0}.card .body p{font-size:.9rem}.card .body span{font-weight:700;font-size:.9rem}.card .body>*{width:100%}
.bodycard{min-height:calc(100vh - 100px);background:#fafafa;transition:.4s}.bodycard .control{padding:2em;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;font-family:"Nunito Sans",sans-serif}@media(max-width: 650px){.bodycard .control{justify-content:center}}.bodycard .control .search{background:#fff;padding:0 1em;display:flex;justify-content:space-around;align-items:center;width:430px;height:50px;font-size:1.25rem;color:#999;box-shadow:0 2px 5px 1px rgba(0,0,0,.1);border-radius:5px}@media(max-width: 650px){.bodycard .control .search{width:100%}}.bodycard .control .search input{padding:1em 1em;width:350px}@media(max-width: 650px){.bodycard .control .search input{width:80%}}.bodycard .control .search input::-webkit-input-placeholder{font-family:"Nunito Sans",sans-serif;font-size:.9rem}.bodycard .control .filter select{font-family:"Nunito Sans",sans-serif;padding:.75em 2em;font-size:1rem;font-weight:500;border-radius:5px;box-shadow:0 1px 5px 1px rgba(0,0,0,.1)}.bodycard .control .filter select option{font-family:"Nunito Sans",sans-serif;font-size:1rem}@media(max-width: 650px){.bodycard .control .filter{margin:1em 0}}.bodycard .body{display:flex;flex-wrap:wrap;justify-content:center}.darkbodycard{background:#202c37;color:#fff}.darkbodycard .control .search{background:#2b3945;color:#fff}.darkbodycard .control .search input{background:#2b3945;color:#fff}.darkbodycard .control .search input::-webkit-input-placeholder{color:#fff}.darkbodycard .control .filter select{background:#2b3945;color:#fff}.darkbodycard .card{background:#2b3945;color:#fff}
.info{font-family:"Nunito Sans",sans-serif;padding:2em;transition:.4s;min-height:calc(100vh - 100px)}@media(max-width: 300px){.info{padding:2em 1em}}.info .back{padding:.5em 1.5em;background:#fff;box-shadow:0 0 5px 5px rgba(0,0,0,.1);font-family:"Nunito Sans",sans-serif;cursor:pointer;transition:.4s;font-weight:600}.info .back i{margin:0 .25em 0 0}.info .back:hover{background:rgba(100,100,100,.1);box-shadow:0 0 1px 1px rgba(0,0,0,.1)}.info .inner-info{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.info .inner-info span{font-size:.9;font-weight:700}.info .inner-info .infoimg{width:400px;height:400px;display:flex;align-items:center}.info .inner-info .infoimg img{width:100%;height:auto}.info .inner-info .col-2{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.info .inner-info .col-2>div{margin:0 2em}@media(max-width: 900px){.info .inner-info .col-2>div{text-align:center;margin:0}}.info .inner-info .col-2 h1{margin:1em 0}.info .inner-info .col-2 p{margin:.5em 0}.info .inner-info .border{display:flex;align-items:center;flex-wrap:wrap}@media(max-width: 900px){.info .inner-info .border{justify-content:center}}.info .inner-info .border ul{margin:1em;display:flex;flex-wrap:wrap;justify-content:center}.info .inner-info .border ul li{border:1px solid #555;padding:.25em;margin:.25em .25em}.info .error404{padding:2em;text-align:center}.info .error404 .in-er{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.info .error404 .in-er h1{margin:0 .25em;font-size:5rem;font-weight:500}.darkinfo{background:#202c37;color:#fff}.darkinfo .back{background:#2b3945;color:#fff}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,html{
  
}
.App{
  max-width: 1920px;
  margin: 0 auto;
}
li{
  list-style: none;
  list-style-type: none;
}
a{
  text-decoration: none;
}
button,input,select{
  border:0;
}
button:focus,input:focus,select:focus{
  outline: none;
}
